.skills {
    min-height: 800px;
    padding: 130px 0 40px 0;
    hr {
        height: 6px;
        border-radius: 3px;
        width: 350px;
        display: block;
        margin: 0 auto;
        background: #fff;
        margin-top: 15px;
        margin-bottom: 40px; }
    &__item {
        width: 350px;
        min-height: 250px;
        margin-bottom: 50px; }
    &__title {
        text-align: center;
        font-family: 'Open Sans', sans-serif;
        font-size: 25px;
        color: #fff;
        font-weight: 700;
        margin-bottom: 25px; }
    &__list {
        list-style-type: none; }
    &__descr {
        text-align: left;
        font-family: 'Open Sans', sans-serif;
        font-size: 20px;
        color: #fff;
        font-weight: 500;
        position: relative;
        margin-bottom: 5px;
        &::before {
           content: "âœ¦";
           position: absolute;
           left: -30px; } }
    &__wrapper {
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap; } }



