.promo {
    padding-top: 100px;
    background: url('../img/grid_bg.png') center (center/cover) no-repeat;
    min-height: 725px;
    &__skills {
        text-align: center;
        margin-top: 150px;
        font-size: 25px;
        font-family: 'Open Sans', sans-serif;
        font-weight: 400; }
    hr {
        width: 700px;
        display: block;
        margin: 0 auto;
        margin-top: 40px; }
    &__title {
        font-family: 'Josefin Sans', sans-serif;
        text-align: center;
        font-size: 68px;
        font-weight: 700;
        text-transform: uppercase;
        margin-top: 60px;
        color: #000; }
    &__descr {
        text-align: center;
        margin-top: 15px;
        font-size: 40px;
        font-family: 'Open Sans', sans-serif; } }
