.works {
    position: relative;
    height: 1100px;
    background-image: linear-gradient(135deg, #f5f7fa 0%, #c3cfe2 100%);
    padding: 130px 0 40px 0;
    overflow: hidden;
    transition: 1s all;
    &_snd {
        height: 1960px; }
    &_trd {
        height: 2830px; }
    &__gear {
        height: 30px;
        display: inline-block;
        margin-left: 10px; }
    &__header {
        display: flex;
        justify-content: center;
        align-items: center; }
    &__wrapper {
        margin-top: 70px;
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap; }
    &__item {
        border: 2px solid black;
        width: 304px;
        height: 350px;
        margin-bottom: 80px;
        transition: 0.5s;
        border-radius: 25px 0 25px 0;
        &:hover {
            transform: scale(110%); } }
    &__img {
        width: 300px;
        height: 250px;
        background-image: linear-gradient(to top, #d9afd9 0%, #97d9e1 100%);
        border-radius: 25px 0 0 0;
        display: flex;
        justify-content: center;
        align-items: center;
        font-size: 30px;
        font-family: 'Comfortaa', cursive;
        color: #000; }
    &__link {
        text-decoration: none; }
    &__descr {
        border-top: 2px solid black;
        text-align: center;
        font-family: 'Comfortaa', cursive;
        font-size: 20px;
        padding: 15px 0 0 0;
        text-transform: uppercase; }
    &__more {
        position: absolute;
        left: 50%;
        bottom: 30px;
        transform: translateX(-50%);
        font-family: 'Josefin Sans', sans-serif;
        font-weight: 400;
        text-transform: uppercase;
        font-size: 20px;
        color: #DC143C;
        cursor: pointer;
        text-decoration: underline; }
    &__more-snd {
        position: absolute;
        left: 35%;
        bottom: 30px;
        font-family: 'Josefin Sans', sans-serif;
        font-weight: 400;
        text-transform: uppercase;
        font-size: 20px;
        color: #DC143C;
        cursor: pointer;
        text-decoration: underline;
        display: none; }
    &__less {
        position: absolute;
        right: 35%;
        bottom: 30px;
        font-family: 'Josefin Sans', sans-serif;
        font-weight: 400;
        text-transform: uppercase;
        font-size: 20px;
        color: #DC143C;
        cursor: pointer;
        text-decoration: underline;
        display: none; }
    &__less-snd {
        position: absolute;
        left: 50%;
        bottom: 30px;
        transform: translateX(-50%);
        font-family: 'Josefin Sans', sans-serif;
        font-weight: 400;
        text-transform: uppercase;
        font-size: 20px;
        color: #DC143C;
        cursor: pointer;
        text-decoration: underline;
        display: none; } }
