.header {
    position: fixed;
    width: 100%;
    height: 100px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 200px;
    background-color: rgba(0, 0, 0, .90);
    z-index: 2;
    &__title {
        display: block;
        text-align: center;
        font-family: 'Comfortaa', cursive;
        font-size: 25px;
        color: #1E90FF;
        cursor: pointer; }
    &__logo {
        text-decoration: none; }
    &__menu {
        display: flex;
        justify-content: space-between;
        align-items: center;
        list-style-type: none; }
    &__link {
        text-decoration: none;
        color: #fff;
        font-family: 'Open Sans', sans-serif;
        font-size: 16px;
        text-transform: uppercase;
        padding: 10px 15px;
        margin-right: 15px;
        border-radius: 20px;
        &:hover {
            background-color: #fff;
            color:#000 {}
            transition: 0.6s all; }
        &_active {
            background-color: #fff;
            color:#000 {}
            transition: 0.6s all; } }
    &__humburger {
        width: 40px;
        height: 40px;
        margin-top: 10px;
        display: none;
        hr {
            width: 40px;
            height: 2px;
            margin-bottom: 10px; } }
    &__panel {
        position: absolute;
        right: 120px;
        background-color: rgba(0, 0, 0, .90);
        top: 100px;
        width: 200px;
        height: 400px;
        display: none;
        &_active {
            display: flex; }

        &-list {
            display: flex;
            width: 200px;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            list-style-type: none;
            padding-top: 40px; }
        &-item {
            margin-bottom: 50px; }
        &-link {
            text-decoration: none;
            color: #fff;
            font-family: 'Open Sans', sans-serif;
            font-size: 14px;
            text-transform: uppercase;
            margin-right: 15px; } } }






