.footer {
    height: 250px;
    background: url('../img/footer_bg.png') center (center/cover) no-repeat;
    padding: 20px 0 0 0;
    &__img {
        width: 80px;
        transition: 0.5s all;
        &:hover {
            transform: scale(110%); } }
    &__social {
        display: flex;
        justify-content: space-between;
        width: 500px;
        margin: 0 auto; }
    &__item {
        width: 75px;
        height: 75px;
        background: linear-gradient(to bottom, rgba(255,255,255,0.15) 0%, rgba(0,0,0,0.15) 100%), radial-gradient(at top center, rgba(255,255,255,0.40) 0%, rgba(0,0,0,0.40) 120%) #989898;
        background-blend-mode: multiply,multiply;
        border-radius: 25px;
        display: flex;
        justify-content: center;
        align-items: center;
        transition: 0.5s all;
        &:hover {
            box-shadow: 3px 3px #FFFF00, -1em 0 .4em #00FF00; } }
    &__links {
        width: 650px;
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin: 0 auto;
        margin-top: 25px; }
    &__link {
        text-decoration: none;
        text-transform: uppercase;
        font-size: 18px;
        font-family: 'Josefin Sans', sans-serif;
        font-weight: 400;
        color: #fff;
        transition: 0.5s all;
        &:hover {
            transform: scale(110%);
            color: blue; } }
    &__descr {
        text-align: center;
        margin-top: 50px;
        color: #00FFFF;
        font-size: 18px;
        font-family: 'Josefin Sans', sans-serif; }
    &__twitch {
        height: 45px;
        width: 45px; } }
