.application {
    min-height: 700px;
    background: url('../img/space_bg.jpg') center (center/cover) no-repeat;
    padding: 130px 0 40px 0;
    &__bg {
        width: 100%;
        height: 100%; }
    &__wrapper {
        display: flex;
        justify-content: space-between; }
    &__img {
        width: 400px;
        height: 560px; }
    &__subwrapper {
        width: 100%;
        height: 250px;
        background-image: linear-gradient(120deg, #89f7fe 0%, #66a6ff 100%);
        display: flex;
        justify-content: center;
        align-items: center;
        border-radius: 15px; }
    &__title {
        color: #00FF00;
        font-size: 35px;
        text-transform: uppercase;
        text-align: center;
        font-family: 'Comfortaa', cursive;
        font-weight: 600;
        margin-bottom: 40px; }
    &__form {
        width: 500px;
        height: 580px;
        display: flex;
        flex-direction: column;
        label {
            text-align: center;
            margin-bottom: 15px;
            font-family: 'Comfortaa', cursive;
            color: #fff; }
        input {
            width: 100%;
            height: 90px;
            margin-bottom: 25px;
            border-radius: 35px;
            border: none;
            background-image: linear-gradient(120deg, #89f7fe 0%, #66a6ff 100%);
            transition: 0.5s all;
            padding: 0 40px;
            font-size: 20px;
            &:hover {
                box-shadow: 3px 3px red, -1em 0 .4em #8A2BE2; }
            &:active {
                outline: 0;
                outline-offset: 0; }
            &:focus {
                outline: 0;
                outline-offset: 0; } }
        button {
            width: 100%;
            height: 100px;
            background-image: linear-gradient(120deg, #89f7fe 0%, #66a6ff 100%);
            border: none;
            border-radius: 15px;
            transition: 0.5s all;
            color: blue;
            font-size: 30px;
            text-transform: uppercase;
            font-family: 'Josefin Sans', sans-serif;
            font-weight: 600;
            &:hover {
                box-shadow: 3px 3px red, -1em 0 .4em #8A2BE2; } } }
    &__text {
        transition: 0.5s all;
        &:hover {
            transition: 0.5s all;
            transform: scale(120%); } } }

