.contacts {
    min-height: 600px;
    padding: 50px 0 40px 0;
    hr {
        height: 6px;
        width: 350px;
        display: block;
        margin: 0 auto;
        background: #fff;
        margin-top: 15px;
        margin-bottom: 80px;
        border-radius: 3px; }
    &__wrapper {
        width: 90%;
        padding: 0 50px;
        display: flex;
        justify-content: space-between;
        margin: 0 auto;
        height: 250px;
        align-items: center; }
    &__item {
        width: 150px;
        height: 150px;
        display: flex;
        justify-content: center;
        align-items: center;
        background-image: linear-gradient(120deg, #a1c4fd 0%, #c2e9fb 100%);
        border-radius: 100%;
        transition: 0.65s all;
        &:hover {
            box-shadow: 3px 3px red, -1em 0 .4em #8A2BE2; } }
    &__img {
        width: 150px;
        transition: 0.65s all;
        &:hover {
            transform: scale(110%); } }
    &__phone {
        text-align: center;
        font-family: 'Inconsolata', monospace;
        font-size: 25px; }
    &__email {
        text-align: center;
        margin-top: 15px;
        font-family: 'Inconsolata', monospace;
        font-size: 30px;
        color: #000; }
    &__number {
        text-decoration: none;
        font-weight: 500;
        color: #000;
        font-size: 30px; } }

.twitch {
    height: 80px;
    width: 80px; }


